.page {
  height: 100%;
}
.page__title {
  font-size: 27px;
  line-height: 30px;
  margin: 0;
  text-align: center;
  padding-top: 73px;
}
@media (min-width: 768px) {
  .page__title {
    font-size: 43px;
    line-height: 50px;
    padding-top: 86px;
  }
}
@media (min-width: 1150px) {
  .page__title {
    font-size: 53px;
    line-height: 57px;
    padding-top: 71px;
  }
}
.page-body {
  min-height: 100%;
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 15px;
  line-height: 24px;
  font-weight: 400;
  color: #000000;
  text-align: left;
  background-color: #FFFFFF;
  padding: 0;
  margin: 0;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
}
.page-header {
  background-color: #FFFFFF;
  font-weight: 700;
}
.page-header__logo {
  height: 79px;
  text-align: center;
  padding-top: 22px;
}
.page-header__logo img {
  width: 86px;
  height: 35px;
}
.page-header__container {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
}
@media (max-width: 767px) {
  .page-header__wrap {
    padding: 0;
  }
}
@media (min-width: 768px) {
  .page-header__logo {
    height: 89px;
    padding-left: 4px;
    padding-top: 25px;
    padding-right: 0;
    min-width: 0;
    width: 48.2%;
    text-align: left;
    background-image: url(../img/fone-logo-tablet.svg);
    background-repeat: no-repeat;
    background-size: 114px 46px;
    background-position: 0px 20px;
  }
  .page-header__logo img {
    width: 98px;
    height: 33px;
  }
  .page-header__fone {
    background: linear-gradient(to bottom, #FFFFFF 89px, rgba(0, 0, 0, 0.1) 90px, #ECECEC 1px, #F1F1F1 3%, #f9f9f9 28%);
  }
}
@media (min-width: 1150px) {
  .page-header__logo {
    text-align: left;
    width: 200px;
    order: 2;
    background-image: url("../img/fone-logo-desktop.svg");
    background-repeat: no-repeat;
    background-size: 142px auto;
    background-position: -13px 50%;
    padding-left: 9px;
  }
  .page-header__logo:hover {
    opacity: 60%;
  }
  .page-header__logo:active {
    opacity: 30%;
  }
  .page-header__logo img {
    width: 104px;
    height: 44px;
  }
}
.main-navigation {
  font-size: 17px;
  line-height: 24px;
  width: auto;
}
.main-navigation__toggle {
  position: absolute;
  top: 0;
  right: 25px;
  z-index: 2;
  display: block;
  width: 30px;
  height: 60px;
  background-color: #FFFFFF;
  cursor: pointer;
  border: none;
  padding: 0;
}
.main-navigation--closed .main-navigation__toggle::before {
  content: "";
  position: absolute;
  top: 32px;
  right: 5px;
  width: 20px;
  height: 2px;
  background-color: #231F20;
  box-shadow: 0 6px 0 0 #231F20, 0 12px 0 0 #231F20;
}
.main-navigation--opened .main-navigation__toggle::before,
.main-navigation--opened .main-navigation__toggle::after {
  content: "";
  position: absolute;
  top: 32px;
  right: 5px;
  width: 20px;
  height: 2px;
  background-color: #000000;
}
.main-navigation--opened .main-navigation__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-navigation--opened .main-navigation__toggle::after {
  transform: rotate(-45deg);
}
.main-navigation--nojs .main-nav__wrapper {
  position: static;
}
.main-navigation--nojs .main-navigation__toggle {
  display: block;
}
.main-navigation--nojs .site-list__item {
  display: block;
}
.main-navigation__list2 {
  display: none;
}
.main-navigation__list2 a {
  list-style: none;
  text-decoration: none;
  color: #000000;
}
.main-navigation__list a {
  list-style: none;
  text-decoration: none;
  color: #000000;
}
.main-navigation__list a:hover {
  color: rgba(0, 0, 0, 0.6);
}
.main-navigation__list a:active {
  color: rgba(0, 0, 0, 0.3);
}
.main-navigation__wraper {
  display: grid;
}
@media (max-width: 767px) {
  .main-navigation--closed .site-list__item {
    display: none;
  }
  .main-navigation--closed .cart {
    display: none;
  }
  .main-navigation__delivery {
    display: none;
  }
}
@media (min-width: 768px) {
  .main-navigation--nojs .main-navigation__toggle {
    display: none;
  }
  .main-navigation__wraper {
    display: flex;
    flex-wrap: wrap;
  }
  .main-navigation__list {
    display: flex;
    flex-wrap: wrap;
    order: 2;
    width: 50%;
  }
  .main-navigation__uzer-list {
    display: flex;
    flex-wrap: wrap;
    order: 1;
    width: 51.8%;
  }
  .main-navigation__delivery {
    margin: 0;
    margin-left: auto;
    padding-top: 19px;
    order: 2;
    font-size: 15px;
    font-weight: 400;
    color: #444444;
  }
}
@media (min-width: 1150px) {
  .main-navigation {
    font-size: 17px;
    line-height: 24px;
  }
  .main-navigation__list {
    order: 1;
    width: 432px;
    font-size: 17px;
  }
  .main-navigation__list2 {
    display: flex;
    flex-wrap: wrap;
    order: 4;
    width: 401px;
  }
  .main-navigation__uzer-list {
    width: 346px;
    order: 3;
  }
  .main-navigation__delivery {
    order: 5;
  }
}
.site-list {
  list-style: none;
  margin: 0;
  padding: 0;
}
.site-list__item a {
  position: relative;
  display: block;
  padding: 28px 0 25px 60px;
  text-decoration: none;
  color: #000000;
  border-top: 1px solid #C6C6C6;
}
.site-list__img-search {
  position: absolute;
  left: 24px;
  fill: #000000;
  width: 5%;
}
.site-list__img-search:hover {
  fill: #63D1BB;
}
.site-list__img-search:active {
  fill: #63D1BB;
  fill-opacity: 0.3;
}
@media (min-width: 768px) {
  .site-list__span {
    display: none;
  }
  .site-list__item {
    width: 50%;
    font-size: 15px;
  }
  .site-list__item a {
    padding-left: 0;
    padding-right: 20px;
    padding-top: 20px;
    border-top: none;
  }
  .site-list__item--search {
    width: 82px;
    flex-shrink: 0;
    border-left: 1px solid rgba(0, 0, 0, 0.1);
    border-right: 1px solid rgba(0, 0, 0, 0.1);
    padding-top: 17px;
  }
  .site-list__img-search {
    width: 18px;
    left: 40%;
  }
}
@media (min-width: 1150px) {
  .site-list__item {
    font-size: 17px;
  }
  .site-list__item:nth-child(1) {
    border-right: 1px solid #C6C6C6;
  }
  .site-list__item--wid {
    width: 182px;
    height: 89px;
  }
  .site-list__item--wid a {
    padding-top: 32px;
  }
  .site-list__item--wid:nth-last-child(1) {
    margin-left: 37px;
  }
}
.news-list {
  list-style: none;
  margin: 0;
  padding: 0;
}
.news-list__item p {
  color: #444444;
}
@media (min-width: 768px) {
  .news-list {
    display: none;
  }
}
@media (min-width: 1150px) {
  .news-list {
    display: block;
    display: flex;
    flex-wrap: wrap;
  }
  .news-list__item {
    padding-top: 18px;
    font-size: 15px;
  }
  .news-list__item:hover a {
    color: rgba(0, 0, 0, 0.6);
  }
  .news-list__item:active a {
    color: rgba(0, 0, 0, 0.3);
  }
}
.page-main__fone {
  background-color: #f9f9f9;
  background-image: url("../img/hero-mobile1.jpg");
  background-repeat: no-repeat;
  background-position: 50%;
  height: 341px;
}
@media (min-resolution: 2dppx) {
  .page-main__fone {
    background-image: url("../img/hero-mobile1@2x.jpg");
    background-size: 343px;
  }
}
@media (min-width: 768px) {
  .page-main__fone {
    height: 556px;
    background-color: #f9f9f9;
    background-image: url("../img/hero-tablet2.jpg");
    background-repeat: no-repeat;
    background-position: 2px 33%;
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .page-main__fone {
    background-image: url("../img/hero-mobile1@2x.jpg");
    background-size: 600px;
    background-position: 85px 33%;
  }
}
@media (min-width: 1150px) {
  .page-main__fone {
    height: 556px;
    background-color: #f9f9f9;
    background-image: url("../img/hero-desktop1.jpg");
    background-repeat: no-repeat;
    background-position: 50% 50%;
  }
}
@media (min-width: 1150px) and (min-resolution: 2dppx) {
  .page-main__fone {
    background-image: url("../img/hero-desktop1@2x.jpg");
    background-size: 1130px;
  }
}
.selection {
  position: relative;
  top: -57px;
  margin-bottom: -57px;
}
.selection__container li {
  padding: 23px 35px 17px 30px;
}
.selection__list {
  list-style: none;
  padding: 0;
  margin: 0;
}
.selection__item a {
  display: flex;
  justify-content: space-between;
  text-decoration: none;
  color: #FFFFFF;
  font-size: 19px;
  line-height: 24px;
  font-weight: 700;
}
.selection__item span {
  width: 60%;
  margin-right: auto;
}
.selection__interior {
  fill: #149080;
  opacity: 50%;
  width: 20%;
}
.selection__interior:hover {
  opacity: 70%;
}
.selection__interior:active {
  opacity: 30%;
}
.selection__item--fone1 {
  background-color: #63D1BB;
}
.selection__item--fone1:hover {
  background-color: #59D5BC;
}
.selection__item--fone1:active {
  background-color: #59D5BC;
}
.selection__item--fone2 {
  background-color: #56C3B2;
}
.selection__item--fone2:hover {
  background-color: #46C1AE;
}
.selection__item--fone2:active {
  background-color: #46C1AE;
}
@media (min-width: 768px) {
  .selection {
    top: -1px;
    margin-bottom: 10px;
    position: relative;
  }
  .selection ::after {
    position: absolute;
    content: "";
    display: block;
    background-image: url('../img/zigzag-fill.svg');
    background-repeat: repeat;
    height: 12px;
    width: 100%;
    top: 109px;
    right: 0;
  }
  .selection__container li {
    padding: 34px 30px 23px 38px;
  }
  .selection__list {
    display: flex;
    flex-wrap: wrap;
    padding: 0 50px;
  }
  .selection__item--fone1 {
    width: 50%;
  }
  .selection__item--fone2 {
    width: 50%;
  }
  .selection__container {
    background-color: #f9f9f9;
  }
}
@media (min-width: 1150px) {
  .selection {
    top: -14px;
    margin-bottom: -14px;
  }
  .selection ::after {
    height: 50px;
    width: 100%;
    top: 99px;
    right: 0;
    background-size: 111%;
  }
  .selection__list {
    display: flex;
    flex-wrap: nowrap;
    margin: 0 auto;
    max-width: 1150px;
  }
  .selection__container li {
    padding-top: 26px;
    padding-bottom: 50px;
  }
  .selection__item span {
    width: 31%;
  }
}
.product-week {
  color: #444444;
  padding-bottom: 37px;
}
.product-week__container {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}
.product-week__container--title {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}
.product-week__container--description {
  width: 100%;
}
.product-week__container--center {
  padding-left: 67px;
  padding-bottom: 21px;
  padding-top: 20px;
}
.product-week__title {
  font-size: 17px;
  line-height: 20px;
  color: #63D1BB;
  text-transform: uppercase;
  margin: 0;
  padding-top: 5px;
  order: 2;
  width: 100%;
}
.product-week__subtitle {
  font-size: 25px;
  line-height: 30px;
  color: #000000;
  margin: 0;
  width: 100%;
  padding-top: 38px;
}
.product-week__text {
  font-weight: 400;
  margin: 0;
  padding-top: 15px;
}
.product-week__table {
  line-height: 20px;
  font-weight: 400;
  border-collapse: collapse;
  border-radius: 1px;
  border-spacing: 0;
  width: 260px;
  margin-bottom: 30px;
  margin-top: 20px;
}
.product-week__table tr {
  border-spacing: 0;
  border-bottom: 1px solid #C6C6C6;
  height: 40px;
}
.product-week__table td:last-child {
  padding-left: 15px;
}
.product-week__price {
  font-size: 17px;
  line-height: 24px;
  font-weight: 700;
  text-align: left;
  width: 100%;
}
.product-week__button {
  width: 260px;
  border: 2px solid #444444;
  padding: 13px;
}
.product-week__button:hover {
  background-color: #444444;
  color: #FFFFFF;
}
.product-week__button:active {
  background-color: #444444;
  color: rgba(255, 255, 255, 0.3);
}
@media (min-width: 768px) {
  .product-week {
    padding-bottom: 0;
  }
  .product-week__container--center {
    font-size: 20px;
    padding: 0;
    padding-left: 70px;
  }
  .product-week__container--position {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
  .product-week__subtitle {
    width: 85%;
    font-size: 43px;
    line-height: 50px;
    padding-top: 42px;
  }
  .product-week__title {
    padding-top: 46px;
    padding-right: 5px;
    max-width: 12%;
    text-align: center;
  }
  .product-week__text {
    padding-top: 40px;
    font-size: 17px;
    line-height: 30px;
  }
  .product-week__table {
    width: 87%;
    margin-top: 36px;
  }
  .product-week__table td:last-child {
    padding-left: 0;
  }
  .product-week__img {
    height: auto;
    width: 100%;
    padding-top: 26px;
    grid-column-start: 2;
    grid-column-end: 3;
    grid-row-start: 1;
    grid-row-end: 3;
  }
  .product-week__price {
    display: grid;
    grid-template-rows: 1fr min-content;
  }
  .product-week__button {
    width: 291px;
    padding: 17px;
